/****************************************************/

body {
  font-family: 'ProximaNova_reg', sans-serif;
}

p {
  color: #767676;
}

h2 {
  color: #2c2e30;
  font-size: 21px;
}

.clearfix:after {
  content: '';
  clear: both;
  display: block;
  height: 0;
  width: 0;
}

ul {
  list-style-type: none;
}

/** ---------------------------------------
 * Slider 
 ----------------------------------------*/

.container {
  width: 100%;
}

.slider-wrapper {
  display: flex;
  z-index: 500;
  position: relative;
  width: 100%;
  height: 100vh;
  background: #FFF;
  overflow: hidden;
}

.slider-wrapper .head__desc {
  position: relative;
  z-index: 999;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  flex-direction: column;
}

.slider-wrapper .head__desc h1 {
  font-size: 56px;
  color: #FFF;
  margin-bottom: 10px;
  font-weight: bold;
  font-family: 'ProximaNova_reg', sans-serif;
}

.slider-wrapper .head__desc h2 {
  font-size: 24px;
  color: #FFF;
  margin-bottom: 84px;
  font-family: 'ProximaNova_reg', sans-serif;
}

.slider-wrapper li {
  display: none;
}

.slider-wrapper .current-slide {
  display: block;
}

.slider-shadow {
  width: 100%;
  height: 15px;
  position: relative;
}

.slider-shadow:after,
.slider-shadow:before {
  content: '';
  position: absolute;
  background: #171c24;
  height: 100%;
  width: 50%;
  left: 10px;
  top: -20px;
  -webkit-transform: rotate(-4deg);
  -ms-transform: rotate(-4deg);
  -o-transform: rotate(-4deg);
  transform: rotate(-4deg);
  -webkit-box-shadow: 0 0 15px 8px #171c24;
  box-shadow: 0 0 15px 8px #171c24;
}

.slider-shadow:before {
  right: 10px;
  left: auto;
  -webkit-transform: rotate(4deg);
  -ms-transform: rotate(4deg);
  -o-transform: rotate(4deg);
  transform: rotate(4deg);
}

.slider-wrapper img {
  position: absolute;
  min-width: 100%;
  max-width: none;
  height: auto;
  top: 0;
  left: 0;
}

/**
 * ---[Caption] ---------------------- 
 **/

.slider-wrapper .caption {
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.65);
  width: 100%;
  padding: 10px;
  color: #FFF;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  -o-transform: translateY(100%);
  transform: translateY(100%);
  opacity: 0;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.slider-wrapper li:hover .caption {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

.slider-wrapper h2 {
  color: #00c5b9;
  font-size: 2em;
  font-weight: 400;
  margin-bottom: 6px;
}

.slider-wrapper p {
  font-size: 1.6em;
  font-weight: 300;
  line-height: 1.4em;
}

/**
 * ---[Botones-Control] ---------------------- 
 **/

.control-buttons {
  position: absolute;
  width: 100%;
  margin-bottom: 95px;
  bottom: 0;
  text-align: center;
  z-index: 999;
}

.control-buttons li {
  cursor: pointer;
  display: inline-block;
  text-indent: -99999px;
  height: 10px;
  width: 10px;
  margin: 0 2px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 2px solid #dadada;
}

.control-buttons li.active {
  background: #fff;
}

.authors {
  text-align: center;
  color: #7a8699;
  display: block;
  font-size: 1.6em;
  font-weight: 300;
  margin-top: 80px;
  font-size: 300;
}

/** ---------------------------------------
 * Responsive 
 ----------------------------------------*/

.mouse {
  position: relative;
  display: flex;
  /* top: auto; */
  bottom: 85px;
  margin: 0 auto;
  z-index: 999;
  width: 19px;
  height: 79px;
}

.navbar {
  position: fixed;
  z-index: 999;
  min-height: 75px;
  border-bottom: 1px solid #7a8699;
  width: 100%;
}

.navbar .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-height: 75px;
}

.navbar .row .logo {
  margin-right: 20px;
}

.navbar .row .navbar__item {
  /* ul {
                list-style:none;
            }
            
            a {
            color:red;
            } */
  /*media_queries*/
}

.navbar .row .navbar__item .fa-bars {
  font-size: 2rem;
  color: #fff;
}

.navbar .row .navbar__item #btn-menu {
  display: none;
}

.navbar .row .navbar__item .menu__item label {
  display: none;
  width: 30px;
  height: 30px;
  padding: 10px;
}

.navbar .row .navbar__item .menu__item label:hover {
  cursor: pointer;
  background: rgba(0, 0, 0, 0.3);
}

.navbar .row .navbar__item .menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.navbar .row .navbar__item .menu a {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.navbar .row .navbar__item .menu a:hover {
  color: #fbb02c;
}

.navbar .row .navbar__item .menu a:last-child {
  border-bottom: none;
}

.navbar .row .navbar__item .dropdown {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  padding: 0;
}

.navbar .row .navbar__item .menu a:hover > .dropdown {
  display: inline-block;
}

.navbar .row__right {
  font-size: 14px;
  font-weight: bold;
  color: #FFF;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.navbar .row__right .num {
  color: #FFF;
}

.head__form {
  display: flex;
  justify-content: space-between;
  width: 950px;
  flex-wrap: wrap;
}

.head__form input {
  border: 0;
  border-bottom: #FFF 1px solid;
  background: none;
  font-size: 18px;
  color: #FFF;
  outline: none;
  flex-basis: 38%;
}

button {
  color: #000;
  background: #fbb02c;
  font-size: 14px;
  font-weight: bold;
  padding: 8px 0;
  border-radius: 3px;
  text-align: center;
  border-style: none;
  height: 36px;
  width: 165px;
}

button:hover {
  color: #fff;
}

.services {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.services .title {
  text-align: center;
  align-items: center;
  padding: 0px;
}

.services .title h1 {
  font-size: 28px;
  font-family: sans-serif;
  font-weight: bold;
  color: #2c2e30;
  padding-bottom: 30px;
}

.services .title .dash {
  background: #fbb02c;
  width: 30px;
  height: 3px;
  margin: 0 auto;
  margin-bottom: 55px;
}

.services .title p {
  font-size: 18px;
  margin-bottom: 50px;
}

.services .kards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
}

.services .kards__items {
  display: flex;
  flex-direction: column;
  flex-basis: 260px;
  border: #e7e7e7 1px solid;
  height: 445px;
  margin-bottom: 100px;
  align-items: center;
  text-align: center;
}

.services .kards__items img {
  margin: 40px 0 18px 0;
  align-self: center;
}

.services .kards__items h2 {
  font-weight: bold;
  line-height: 30px;
  margin: 0 40px;
  min-height: 60px;
}

.services .kards__items p {
  font-size: 14px;
  line-height: 20px;
  margin: 14px 40px;
}

.services .kards__items button {
  margin-top: auto;
  margin-bottom: 40px;
  width: 80%;
}

.title {
  text-align: center;
  align-items: center;
  padding: 70px 0 15px;
}

.title h1 {
  font-size: 28px;
  font-family: sans-serif;
  font-weight: bold;
  color: #2c2e30;
  padding-bottom: 30px;
}

.title .dash {
  background: #fbb02c;
  width: 30px;
  height: 3px;
  margin: 0 auto;
  margin-bottom: 55px;
}

.title p {
  font-size: 18px;
  margin-bottom: 50px;
}

.ben {
  background: #f3f3f3;
  margin-bottom: 150px;
}

.benefits__items {
  display: flex;
  padding-bottom: 100px;
  flex-wrap: wrap;
  justify-content: space-between;
}

.benefits__items ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  width: 50%;
}

.benefits__items ul li {
  padding: 0 0 60px 0;
}

.benefits__items ul li img {
  float: left;
  margin: 0 30px 0 0;
}

.benefits__items ul li h2 {
  margin: 10px 0 15px 100px;
  font-weight: bold;
}

.benefits__items ul li p {
  margin-left: 100px;
  font-size: 14px;
}

.statistics {
  background: url(../img/statistics_bg.jpg) center no-repeat;
  background-size: cover;
  padding: 100px 0;
}

.statistics__items ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.statistics__items ul li {
  text-align: center;
}

.statistics__items ul li h2 {
  font-size: 35px;
  font-weight: bold;
  color: #FFF;
}

.statistics__items ul li p {
  font-size: 16px;
  color: #fff;
}

.statistics__items ul li img {
  height: 60px;
}

.filter #btn_area {
  text-align: center;
  margin: 40px 0 30px 0;
}

.filter .btn {
  padding: 10px 25px;
  margin: 0 6px;
  background: #e5e5e5;
  display: inline-block;
  font-size: 0.9em;
  transition: 0.3s ease all;
}

.filter .btn:hover {
  background: #e1e1e1;
  cursor: pointer;
}

.filter .active {
  background: #ffffff;
}

.filter #parent {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}

.filter .item {
  display: inline-block;
  margin: 0 10px 20px 10px;
  width: calc(33.333333333333336% - 30px);
}

.filter .item img {
  height: 360px;
  object-fit: cover;
}

.filter h4 {
  text-align: center;
  line-height: 80px;
  font-weight: normal;
}

@media screen and (max-width: 1100px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .head__form {
    width: 80%;
  }

  .head__form input {
    flex-basis: 35%;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .navbar .row {
    justify-content: center;
  }

  .navbar .row__right {
    display: none;
  }

  .services .kards__items {
    flex-basis: 350px;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .slider-wrapper .head__desc h1 {
    margin-top: 130px;
    font-size: 46px;
  }

  .slider-wrapper .head__desc h2 {
    font-size: 20px;
  }

  .control-buttons {
    margin-bottom: 125px;
  }

  .mouse {
    bottom: 115px;
  }

  .navbar .row {
    justify-content: space-between;
  }

  .navbar .row .navbar__item .menu a {
    font-size: 14px;
    border-radius: 3px;
  }

  .navbar .row .navbar__item .menu a:hover {
    color: #fff;
    background: #fbb02c;
  }

  .navbar .row .navbar__item .menu a:last-child {
    border-bottom: 1px solid #7a8699;
  }

  .head__form {
    flex-direction: column;
  }

  .head__form input {
    flex-basis: 0;
    margin-bottom: 10px;
    padding-top: 5px;
  }

  button {
    width: 100%;
  }

  .benefits__items ul {
    width: 100%;
  }

  .statistics__items ul li {
    min-width: 50%;
    margin-bottom: 30px;
  }
}

@media (max-width: 768px) {
  .navbar .row .navbar__item .menu__item label {
    display: block;
  }

  .navbar .row .navbar__item .menu {
    position: absolute;
    width: 100%;
    right: -100%;
    transition: all 0.3s;
    display: flex;
    flex-direction: column;
    margin-top: 12px;
  }

  .navbar .row .navbar__item nav a {
    display: block;
  }

  .navbar .row .navbar__item #btn-menu:checked ~ .menu {
    right: 0;
  }
}

@media screen and (max-width: 560px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .slider-wrapper .head__desc h1 {
    font-size: 36px;
    text-align: center;
  }

  .slider-wrapper .head__desc h2 {
    font-size: 18px;
    text-align: center;
  }

  .control-buttons {
    margin-bottom: 20px;
  }

  .mouse {
    display: none;
  }

  .statistics__items ul li {
    min-width: 100%;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 320px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .control-buttons {
    margin-bottom: 10px;
  }
}