/** ---------------------------------------
 * Slider 
 ----------------------------------------*/
.container {
    width: 100%;
    //margin: 40px auto 0;
}

.slider-wrapper {
    display: flex;
    z-index: 500;
    position: relative;
    width: 100%;
    height: 100vh;
    background: #FFF;
    //border: 5px solid #4b5973;
    overflow: hidden;

    .head__desc {
        position: relative;
        z-index: 999;
        display: flex;
        //text-align: center;
        //justify-content: center;
        align-items: center;
        width: 100%;
        justify-content: center;
        flex-direction: column;

        h1 {
            font-size: 56px;
            color: #FFF;
            margin-bottom: 10px;
            font-weight: bold;
            .reg();
            //.size(3);

            .sm-block( {
                margin-top: 130px;
                font-size: 46px;
            }

            );

            .xs-block( {
                font-size: 36px;
                text-align: center;
            }

            );
        }

        h2 {
            font-size: 24px;
            color: #FFF;
            margin-bottom: 84px;
            .reg();

            .sm-block( {
                font-size: 20px;
            }

            );

            .xs-block( {
                font-size: 18px;
                text-align: center;
            }

            );
        }

    }
}


.slider-wrapper li {
    display: none;
}

.slider-wrapper .current-slide {
    display: block;
}

.slider-shadow {
    width: 100%;
    height: 15px;
    position: relative;
}

.slider-shadow:after,
.slider-shadow:before {
    content: '';
    position: absolute;
    background: #171c24;
    height: 100%;
    width: 50%;
    left: 10px;
    top: -20px;
    -webkit-transform: rotate(-4deg);
    -ms-transform: rotate(-4deg);
    -o-transform: rotate(-4deg);
    transform: rotate(-4deg);
    -webkit-box-shadow: 0 0 15px 8px #171c24;
    box-shadow: 0 0 15px 8px #171c24;
}

.slider-shadow:before {
    right: 10px;
    left: auto;
    -webkit-transform: rotate(4deg);
    -ms-transform: rotate(4deg);
    -o-transform: rotate(4deg);
    transform: rotate(4deg);
}

.slider-wrapper img {
    position: absolute;
    //width: 100%;
    min-width: 100%;
    // object-fit: none;
    max-width: none;
    height: auto;
    top: 0;
    left: 0;

    // .md-block({
    //     object-fit: none;
    //     height: 100%;;
    // }); 

}

/**
 * ---[Caption] ---------------------- 
 **/
.slider-wrapper .caption {
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.65);
    width: 100%;
    padding: 10px;
    color: #FFF;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
    -o-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;

}

.slider-wrapper li:hover .caption {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
}


.slider-wrapper h2 {
    color: #00c5b9;
    font-size: 2em;
    font-weight: 400;
    margin-bottom: 6px;
}

.slider-wrapper p {
    font-size: 1.6em;
    font-weight: 300;
    line-height: 1.4em;

}


