                );
            }

            .menu a:last-child {
                border-bottom: none;

                .sm-block( {
                    border-bottom: 1px solid #7a8699;
                }

                );
            }



            .dropdown {
                display: none;
                position: absolute;
                top: 100%;
                left: 0;
                background: #fff;
                padding: 0
            }

            .menu a:hover>.dropdown {
                display: inline-block;
            }

            /*media_queries*/




            @media (max-width: 768px) {
                .menu__item label {
                    display: block;
                }

                .menu {
                    position: absolute;
                    //background: #a2acb4;
                    width: 100%;
                    right: -100%;
                    transition: all 0.3s;
                    display: flex;
                    flex-direction: column;
                    margin-top: 12px;
                }

                nav a {
                    display: block;
                    //border-top: 1px solid #fff;
                }

                #btn-menu:checked~.menu {
